.assignment{
    padding: 32px;
    width: 76.85%;
}
.tilte{
    color: #212529;
    h2{
        font-size: 24px;
        line-height: 33px;
        margin-right: 50px;
    }
    h3{
        font-size: 16px;
    }
    .search{
        margin-right: 26px;
    }
}
.arrow{
    margin-left: 9px;
    a{
        &:hover{
            color:#28FF28;
        }
    }  
}
.assignment-title{
    margin-top: 48px;
    margin-bottom: 8px;
    a{
        color: #6C757D;
    }
    h2{
        font-size: 16px;
    }
}
.assignment-content{
    span{
        display: block;
        font-size: 12px;
        line-height: 15px;
        color: #6C757D;
        font-weight: 400;
        margin-bottom: 4px;
        text-align: left;
    }
    p{
        font-size: 16px;
        line-height: 19px;
        color: #212529;
    }
}
.content-left{
    width: 34.8%;
    
    li{
        border: 1px solid #CED4DA;
        border-top:none ; 
        
    }
    &:first-child{
        border-top: 1px solid #CED4DA;
    }
    
    a{
        display: block;
        padding: 12px;
        &:hover{
            background-color: #F8F9FA;
        }
    }
}
.content-right{
    width: 63.52%;
    
    li{
        padding: 12px 16px;
        border: 1px solid #CED4DA;
        border-top:none ; 
        &:first-child{
            border-top: 1px solid #CED4DA;
            background-color: #F8F9FA;
        }
    }
    .content-right-icon{
        font-size: 24px;
        color: black;
        font-weight: bold;
    }
    
}