*,*::before,*::after {
    box-sizing: border-box;
  }
  body{
    text-align: center;
    font-family: 'Lato',serif;
    line-height: 1.5;
    font-weight: bold;
    /*overflow-y: scroll;
    overflow-x: hidden;*/
  }
  a{
    text-decoration: none;
  }
  img{
    vertical-align: middle;
    max-width:100%;
    height: auto;
  }
  .container{
    width: 1280px;
    margin: 0 auto;
    height: 100vh;
  }
  .roboto{
    font-family: 'roboto',serif;
  }
  .d-flex{
    display: flex;
  }
  .justify-content-between{
    justify-content: space-between;
  }
  .align-items-center{
    align-items: center;
  }
  .flex-row{
    flex-direction: row;
  }
  .flex-column{
    flex-direction: column;
  }